import React from "react"

import Layout from "../components/layout"
import { graphql } from "gatsby"
import projectsStyle from "./projects.module.scss"
import { documentToReactComponents } from "@contentful/rich-text-react-renderer"

export const query = graphql`
  query {
    allContentfulProject(sort: { fields: Date, order: DESC }) {
      edges {
        node {
          title
          url
          organization
          body {
            body
          }
          Date(formatString: "MMMM, YYYY")
          image {
            resize(width: 300, height: 300) {
              src
            }
          }
        }
      }
    }
  }
`

const ProjectPage = props => {
  return (
    <div>
        <h2 className="naviTitle">Projects</h2>
        <ol className={projectsStyle.projects}>
          {props.data.allContentfulProject.edges.map(edge => {
            return (
              <li>
                <h2 className={projectsStyle.projectName}>{edge.node.title}</h2>
                <p className={projectsStyle.projectDate}>{edge.node.Date}</p>
                {/* <p>{edge.node.organization}</p> */}
                <div className={projectsStyle.contents}>
                  <img
                    className={projectsStyle.img}
                    src={edge.node.image.resize.src}
                    art={edge.node.title}
                  ></img>
                  <p className={projectsStyle.body}>{edge.node.body.body}</p>
                </div>
              </li>
            )
          })}
        </ol>
    </div>
  )
}

export default ProjectPage
